@import url("https://fonts.googleapis.com/css2?family=Oxygen+Mono&display=swap");

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

:root {
  --main-color: black;
  --helper-color-1: white;
  --helper-color-2: rgb(95, 19, 95);
  --helper-color-2--darker: rgb(66, 0, 66);
  --helper-color-2--brighter: rgb(161, 0, 161);
  --border-radius: 3px;
}
body {
  padding: 0;
  margin: 0;
  position: relative;
  overflow-x: hidden;
  font-family: "Oxygen Mono", monospace;
  background-color: var(--main-color);
  // &::before {
  //   position: absolute;
  //   top: 0;
  //   left: 0;
  //   content: "";
  //   width: 100%;
  //   height: 100vh;
  //   filter: grayscale(1);
  //   background-attachment: fixed;
  //   background-color: var(--main-color);
  //   background-image: url("./assets/code.jpg");

  //   background-repeat: no-repeat;
  //   background-size: contain;
  // }
}
.page-wrapper {
  position: relative;
  overflow-x: hidden;
}

h1,
h2,
p {
  margin: 0;
}
.center-item {
  display: flex;
  justify-content: center;
}
.section-padding {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

@media only screen and (max-width: 1200px) {
  body {
    &::before {
      background-size: cover;
      background-position: center;
    }
  }
}
